import React from "react";

export function capitalizeFirstLetter(string: string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export function transformTextToHighlight(originText: string, key: string = "") {
  const highlightKey = new RegExp(`(${key})`, "ig");

  const tagsList = originText.split(highlightKey).map((text, index) =>
    text.toLocaleLowerCase() === key?.toLocaleLowerCase() ? (
      <span key={index} className="highlight">
        {text}
      </span>
    ) : (
      text
    )
  );

  return tagsList;
}
