import React from "react";
import { useSiteMetadata } from "@tool/useSiteMetadata";
import { capitalizeFirstLetter } from "@tool/functions";

type SeoContainerType = {
  title?: string;
  description?: string;
  pathname?: string;
  subtitle?: string | null;
  children?: React.ReactNode;
};

export const SeoContainer = ({
  title,
  description,
  pathname,
  subtitle = null,
  children,
}: SeoContainerType) => {
  const {
    title: defaultTitle,
    description: defaultDescription,
    siteUrl,
  } = useSiteMetadata();

  const seo = {
    title: title || defaultTitle,
    description: description || defaultDescription,
    url: `${siteUrl}${pathname || ``}`,
    // twitterUsername,
  };

  const seoTitle = subtitle
    ? `${seo.title} | ${capitalizeFirstLetter(subtitle)}`
    : seo.title;

  return (
    <>
      <title>{seoTitle}</title>
      <meta name="description" content={seo.description} />
      {/* <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={seo.title} />
      <meta name="twitter:url" content={seo.url} />
      <meta name="twitter:description" content={seo.description} />
      <meta name="twitter:image" content={seo.image} />
      <meta name="twitter:creator" content={seo.twitterUsername} /> */}
      <link
        rel="icon"
        href="data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100'><text y='0.9em' font-size='90'>‚òÄÔ∏è</text></svg>"
      />
      {children}
    </>
  );
};
