import React from "react";
import { graphql, PageProps } from "gatsby";
import styled from "styled-components";
import { mdCss } from "@layouts/mdCss";
import { device } from "@layouts/styleConfig";
import Comments from "@components/Comments";
import { SeoContainer } from "@components/Container/seoContainer";
import { useTranslation } from "gatsby-plugin-react-i18next";

const Layout = styled.div.withConfig({ componentId: "BlogPost_Layout" })`
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  padding: 32px 16px;
  color: ${({ theme }) => theme.color};
  .post-title {
    font-size: 32px;
    font-weight: 800;
    grid-column-start: 3;
    grid-column: 1/-1;
  }
  .post-date {
    grid-column: 1/-1;
    color: ${({ theme }) => theme.postColor};
    margin-top: 8px;
  }

  img {
    grid-column: 1/-1;
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-top: 24px;
    border-radius: 16px;
  }

  .post-content {
    grid-column: 1/-1;
    color: ${({ theme }) => theme.contentColor};
    background-color: ${({ theme }) => theme.bgColor};
    backdrop-filter: blur(15px);
    ${mdCss};
  }

  .post-comments {
    border-top: 0.5px solid ${({ theme }) => theme.color};
    margin-top: 32px;
    padding-top: 16px;
    grid-column: 1/-1;
  }

  .post-image-source {
    margin-top: 4px;
    grid-column: 1/-1;
    font-size: 14px;
    font-style: italic;
    color: ${({ theme }) => theme.hintColor};
  }

  @media ${device.tablet} {
    .post-title {
      grid-column: 2/-2;
      font-size: 36px;
    }
    .post-date {
      grid-column: 2/-2;
    }

    img {
      grid-column: 2/-2;
      height: 300px;
    }

    .post-content {
      grid-column: 2/-2;
    }

    .post-comments {
      margin-top: 64px;
      padding-top: 32px;
      grid-column: 2/-2;
    }

    .post-image-source {
      grid-column: 2/-2;
    }
  }

  @media ${device.laptop} {
    .post-title {
      grid-column: 3/-3;
      font-size: 40px;
    }
    .post-date {
      grid-column: 3/-3;
    }

    img {
      grid-column: 3/-3;
      height: 400px;
    }

    .post-content {
      grid-column: 3/-3;
    }

    .post-comments {
      grid-column: 3/-3;
    }

    .post-image-source {
      grid-column: 3/-3;
    }
  }
`;

type templatePostType = {
  blog: {
    frontmatter: {
      title: string;
      date: string;
      image_cover: string;
    };
    html: string;
  };
  metadata?: {
    updatedAt: Date;
    featured?: boolean;
  };
};

export default ({ data }: PageProps<templatePostType>) => {
  const {
    title,
    date,
    image_cover,
    // tags,
    // type,
  } = data.blog.frontmatter;
  const html = data.blog.html;
  const { t } = useTranslation();
  return (
    <Layout>
      <div className="post-title">{title}</div>
      <div className="post-date">{date.split("T")[0]}</div>
      <img src={image_cover} alt={title} />
      <span className="post-image-source">{t("image-source")} </span>
      <div
        className="post-content"
        dangerouslySetInnerHTML={{ __html: html ?? "" }}
      />
      <div className="post-comments">
        <Comments />
      </div>
    </Layout>
  );
};

export const query = graphql`
  query ($language: String!, $name: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    blog: markdownRemark(
      fields: {
        locale: { eq: $language }
        name: { eq: $name }
        sourceInstanceName: { eq: "blog" }
      }
    ) {
      frontmatter {
        title
        date
        image_cover
      }
      html
    }
  }
`;

export const Head = ({ data }: PageProps<templatePostType>) => {
  const subtitle = data.blog.frontmatter.title;
  return <SeoContainer subtitle={subtitle} pathname={`/blog/${subtitle}`} />;
};
